import React from 'react';

import ContentWrapper from '../ContentWrapper';
import PortfolioItem from './PortfolioItem';

import gpImage from '../../image/pfImage-GP.JPG';
import smImage from '../../image/pfImage-SM.JPG';
import calImage from '../../image/pfImage-Calendar.jpg';

import './index.css';

const PortfolioInfoList = [
    {
        image: gpImage,
        title: '실시간 상호작용 증진을 위한 웹 판서 서비스 개발',
        term: '18.03 ~ 18.06',
        desc: '강의에 사용할 때 사용자들(강사,학생)간의 상호작용을 위해, 강사가 하는 작업의 실시간 공유, 학생의 개인 필기 제출 등의 기능을 추가한 웹 서비스를 개발',
        github: 'https://github.com/Normaltic/18_2_Graduation_Project.git',
    },
    {
        image: smImage,
        title: '웹 기반 판서 페이지 개발',
        term: '17.10 ~ 18.01',
        desc: '스마트 칠판과 유사한 기능을 가지고, 접속한 사용자들의 입력이 실시간으로 공유되는 HTML5 Canvas 기반의 웹 판서 페이지를 개발',
        github: 'https://github.com/Normaltic/smartboard',
    },
    {
        image: calImage,
        title: '일정 조율 캘린더 개발',
        term: '17.09 ~ 17.12',
        desc: '기존 캘린더에서, 여러 사용자가 참여하는 일정을 계획할 때, 조율 및 의사소통을 할 수 있는 기능 외 몇 가지 편의 기능을 추가한 캘린더 서비스를 개발',
        github: 'https://github.com/Normaltic/calendar-project',
    }
]

const PortfolioList = (props) => {

    return (
        <ContentWrapper className='PortfolioList-root'>
                {
                    PortfolioInfoList.map( (item) => (
                        <PortfolioItem {...item} />
                    ))
                }
        </ContentWrapper>
    )
}

export default PortfolioList;