import React from 'react';

import githubIcon from '../../image/git.svg';

const PortfolioItem = (props) => {

    return (
        <div className='PortfolioItem-root'>
            <img className='PortfolioItem-image' src={props.image} alt='No Image'/>
            <div className='PortfolioItem-contentWrapper'>
                <p className='PortfolioItem-title'>{props.title}</p>
                <p className='PortfolioItem-term'>{props.term}</p>
                <p className='PortfolioItem-desc'>{props.desc}</p>
                <p className='PortfolioItem-github'>
                    <a href={props.github} target=':_blank'><img style={{verticalAlign: 'middle'}} src={githubIcon} width='19px' height='19px' />Github</a>
                </p>
            </div>
        </div>
    )
}

export default PortfolioItem;